/**
** Footer Styles
**
**/

.footer {
  position: relative;
  clear: both;
  margin-top: 20px;
  padding: 30px 20px 100px;
  background: #7d7972;

  @include desktop {
    min-width: 1014px;
    padding: 30px 21px 100px 20px;
  }

  &-content {
    max-width: 1312px;
    margin: 0 auto;
    padding: 0;

    @include desktop {
      padding: 0;
      width: 100%;
    }

    .footerlinks {
      padding: 0px;

      @include desktop {
        padding: 0 0 0 97px;
      }

      ul {
        list-style: none;
        padding: 0;

        @include tablet-and-up {
          margin: 0 20px;
          &:first-of-type {
            margin-left: 0px;
            float: left;
            width: 35%;
          }
          &:nth-of-type(2) {
            float: left;
          }
          &:nth-of-type(3) {
            clear: both;
            margin-left: 0px;
            width: 35%;
            float: left;
          }
          &.last {
            float: left;
          }
        }

        @include desktop {
          float: left;
          width: 25% !important;

          &:nth-of-type(3) {
            margin-left: 20px;
            clear: none;
          }
        }

        li {
          margin: 0px 0px 16px;
          display: none;

          @include desktop {
            display: list-item;
          }

          &.footer1 {
            display: list-item;
          }

          a {
            font-weight: 300;
            font-size: 15px;
            color: #FFF;
            text-decoration: none;

            &:hover {
              text-decoration: underline;
            }
          }
        }
      }
    }

    .socialmedia {
      position: absolute;
      right: 0px;
      list-style-type: none;
      text-align: right;
      margin: 0 20px 0 0;

      li {
        display: inline-block;

        a {
          display: block;
          color: #FFF;
          font-size: 24px;
          text-decoration: none;

          @include desktop {
            opacity: .5;
            @include transition(all .1s ease);
          }

          &:hover {
            opacity: 1;
          }

          .replaced-text {
            /* h5bp technique */
            border: 0;
            clip: rect(0 0 0 0);
            height: 1px;
            margin: -1px;
            overflow: hidden;
            padding: 0;
            position: absolute;
            width: 1px;
          }
        }

        [class^="icon-"], 
        [class*=" icon-"] {
          padding: 0;
        }
        
        [data-icon]:before, 
        [class^="icon-"]:before, 
        [class*=" icon-"]:before {
          margin: 0 5px 0 0 !important;
        }
      } 
    }
      
    .copyright {
      clear: both;
      text-align: center;
      background: url(http://cdn-prod.www.aws.nypl.org/sites/all/themes/nypl_new/images/main-logo-lion.png) no-repeat;
      background-size: 60px;
      background-position: 50%;
      
      p {
        max-width: none;
        padding-top: 30px;
        padding-bottom: 140px;
        color: #FFF;
        font-size: 14px;
        font-weight: 300;
        letter-spacing: .07em;
        line-height: 2em;
      }
    }
  }
}



